import {HttpClient, HttpParams} from '@angular/common/http';
import {Observable} from 'rxjs';

export class GridService<Tin, Tout> {

  constructor(private http: HttpClient) {
  }

  /**
   *
   * @param apiAddress
   * @param params
   */
  getData(apiAddress: string, params: HttpParams): Observable<Tout> {
    return this.http.get<Tout>(apiAddress, {params});
  }

  /**
   *
   * @param apiAddress
   * @param body
   */
  postData(apiAddress: string, body: Tin): Observable<Tout> {
    return this.http.post<Tout>(apiAddress, body);
  }

}
